.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__menu {
    width: 100%;
    text-align: end;
    padding: 12px 0;
    border-bottom: 1px solid #fff;
}

.header__link {
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
    margin-left: 35px;
    transition: color .2s;
}

.header__link:hover {
    color: #86aff2;
}

@media screen and (max-width: 800px) {
    .header {
        display: block;
        text-align: center;
    }

    .header__logo {
        width: 130px;
    }

    .header__menu {
        margin-top: 20px;
        border-top: 1px solid #fff;
        text-align: center;
    }

    .header__link {
        margin: 20px;
        display: block;
    }
}